@import url(https://fonts.googleapis.com/css2?family=Itim&family=Noto+Sans+TC:wght@400;700&display=swap);
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  padding: 0;
  font-family: itim;
  font-weight: 700;
  background: url(/static/media/moon-surface.2ac524fe.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}

header {
  font-size: 46px;
  text-align: center;
  background-color: rgb(0, 0, 0);
  height: 80px;
  line-height: 80px;
  color: yellow;
}

.container {
  width: 1180px;
  margin: 0 auto;
}
.login-form {
    font-weight: normal;
    width: 300px;
    margin: 100px auto;
    text-align: center;
    border-radius: 10px;
    box-shadow: -8px 8px 15px #ccc;
    background: rgba(21, 24, 17, 0.6);
}

form {
    padding: 20px 15px;
}

.login, .pass {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    color: yellow;
}

.login-btn {
    margin-top: 10px;
    text-decoration: none;
    color: yellow;
    display: inline-block;
    border: 1px solid yellow;
    padding: 3px 6px;
    border-radius: 3px;
}

.login-btn:hover {
    color: rgb(45, 82, 48);
    background-color: yellow;
}
